import React, { useState, useEffect, useRef } from 'react';
import { View, Text, FlatList, TextInput, TouchableOpacity, StyleSheet, BackHandler } from 'react-native';
import _ from 'lodash';
import data from './data';

const SearchApp = () => {
  const [search, setSearch] = useState('');
  const [filteredData, setFilteredData] = useState([]);
  const [sortField, setSortField] = useState(null);
  const [sortDirection, setSortDirection] = useState('asc');
  const [showScrollToTop, setShowScrollToTop] = useState(false);
  const flatListRef = useRef(null);

  useEffect(() => {
    let filteredItems = data.filter(item =>
      item.name.toLowerCase().includes(search.toLowerCase()) ||  item.place.toLowerCase().includes(search.toLowerCase())
    );

    if (sortField) {
      filteredItems = _.orderBy(filteredItems, [sortField], [sortDirection]);
    }

    setFilteredData(filteredItems);
  }, [search, sortField, sortDirection]);

  useEffect(() => {
    const handleScroll = ({ nativeEvent }) => {
      const { contentOffset, contentSize, layoutMeasurement } = nativeEvent;
      const reachedBottom = contentOffset.y + layoutMeasurement.height >= contentSize.height - 50; // Adjust as needed

      setShowScrollToTop(reachedBottom);
    };

    const backHandler = BackHandler.addEventListener('hardwareBackPress', () => {
      if (showScrollToTop) {
        // Scroll to top when the back button is pressed if the "Scroll to Top" button is visible
        flatListRef.scrollToOffset({ offset: 0, animated: true });
        return true;
      }
      return false;
    });

    return () => {
      backHandler.remove();
    };
  }, [showScrollToTop]);



  const toggleSortDirection = () => {
    setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
  };

  const handleSort = (field) => {
    if (sortField === field) {
      toggleSortDirection();
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  return (
    <View style={styles.container}>
      <View style={styles.inputContainer}>
        <TextInput
          placeholder="Search..."
          value={search}
          onChangeText={text => setSearch(text)}
          style={styles.input}
        />
      </View>
      <FlatList
     ref={flatListRef}
        data={filteredData}
        keyExtractor={item => item.index}
        renderItem={({ item }) => (
          <View style={styles.item}>
            <View style={styles.itemInfo}>
              <Text>{item.name}</Text>
              <Text>{item.place}</Text>
              <Text>{item.amount}</Text>
            </View>
          </View>
        )}
        ListHeaderComponent={() => (
          <View style={styles.header}>
            <TouchableOpacity onPress={() => handleSort('name')}>
              <Text style={styles.headerText}>Name</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => handleSort('place')}>
              <Text style={styles.headerText}>Place</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => handleSort('amount')}>
              <Text style={styles.headerText}>Amount</Text>
            </TouchableOpacity>
          </View>
        )}
        onScroll={({ nativeEvent }) => {
          // Detect scrolling to hide/show the "Scroll to Top" button
          if (nativeEvent.contentOffset.y > 300) {
            setShowScrollToTop(true);
          } else {
            setShowScrollToTop(false);
          }
        }}
      />
      {showScrollToTop && (
        <TouchableOpacity
          style={styles.scrollToTopButton}
          onPress={() => {
            flatListRef.scrollToOffset({ offset: 0, animated: true });
          }}
        >
          <Text style={styles.scrollToTopButtonText}>Scroll to Top</Text>
        </TouchableOpacity>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 16,
  },
  inputContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 16,
  },
  input: {
    flex: 1,
    height: 40,
    borderColor: 'gray',
    borderWidth: 1,
    paddingHorizontal: 8,
    marginRight: 8,
  },
  item: {
    padding: 8,
  },
  itemInfo: {
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 8,
    borderBottomWidth: 1,
    borderBottomColor: 'gray',
  },
  headerText: {
    fontWeight: 'bold',
  },
});

export default SearchApp;
